import React from "react";
import styles from "../styles/Home.module.css"

function About() {
  return (
    <div className="about">
          <div class={styles.major}>
          <img
              class={styles.photo}
              src="images/photo.jpg"
              alt=""
            />
      </div>
          <div class="col-lg-5">
            <h1 class={styles.infodescription}>About</h1>
            <p class={styles.infodescription2}>
            I am a Sophomore at the University of California, Berkeley, intending Computer Science. I am originally from Vancouver!
            </p>
          </div>
        </div>
    //   </div>
    // </div>
  );
}

export default About;