import React from "react";
import styles from "../styles/Home.module.css"

function Projects() {
  return (
    <div className="projects">
      <div class="container">
        <div class="row align-items-center my-5">
          <div class="col-lg-7">
            <img
              class={styles.ants}
              src="images\Ants.jpg"
              alt=""
            />
          </div>
          <div class="col-lg-5">
            <h1 class={styles.infodescription}>Projects</h1>
            <h2 class={styles.projectname}>Build-A-Bear</h2>
            <p class = {styles.projectdescription}>
              Learning design principles and front-end web development to build a personal website.
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Projects;